.container {
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: #c0c0c0;
    content: center;
}

.col-1{
    padding-top: 50%;
    padding-bottom: 50%;
    text-align: right;
    padding-right: 10px;
}

.btn {
    border: 2px solid black;
    background-color: white;
    color: black;
    padding: 14px 28px;
    font-size: 16px;
    cursor: pointer;
  }
  
  /* Green */
  .success {
    border-color: blue;
    color: blue;
    border-radius: 12px;
  }
  
  .success:hover {
    background-color: #555555;
    color: white;
  }

